	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Puzzled and Bemused","date":"07 Dec 2005","slug":"/2005-12-07-puzzled-and-bemused/","tags":"Y2005"},"html":"<p>I was reminded of PapaScott's <a href=\"http://www.papascott.de/archives/1999/12/05/\">6th anniversary</a> (and indeed that of all early adopters of editthispage.com) not by <a href=\"http://www.2020hindsight.org/2005/12/06/6-year-blogiversary/\">Susan's post</a>, but by a couple of <a href=\"http://www.papascott.de/archives/2005/12/05/cats-in-sinks/#comment-2188\" title=\"PapaScott &raquo; Blog Archive &raquo; Cats in Sinks\">strange comments</a> that got past my spam filter. They came at the same time, from different IP addresses, were posted to a throw-away entry, seem to be written by the same person, look like spam, but were aware that PapaScott was 6 years old that day. Color me puzzled and bemused, and thank you, I think. And <a href=\"http://www.papascott.de/archives/2004/12/05/editthispage-again/\">congratulations</a> to ETP veterans everywhere! We're still not <a href=\"http://www.papascott.de/archives/2005/10/12/serendipita/\">rich</a>, but maybe we can forge an entry on Wikipedia giving us credit for this weblog thing.</p>"},"next":{"fields":{"title":"Head Butt","date":"07 Dec 2005","slug":"/2005-12-07-head-butt/"}},"previous":{"fields":{"title":"VPN Part 2","date":"07 Dec 2005","slug":"/2005-12-07-vpn-part-2/"}}},{"node":{"fields":{"title":"Head Butt","date":"07 Dec 2005","slug":"/2005-12-07-head-butt/","tags":"Y2005"},"html":"<p>In the \"Sport is stranger than real life\" department, in a particularly <a href=\"http://www.spiegel.de/sport/fussball/0,1518,388956,00.html\">chippy Bundesliga match</a> last night between Duisburg and K&ouml;ln, the Duisburg coach starts arguing with an opposing player on the sidelines (strange enough). The coach then gives the player a head butt, turns to the TV camera, and falls theatrically to the ground. This is soccer, not American football, nobody's wearing any helmets! Even if you don't read German, you can see the pictures at <a href=\"http://www.allesaussersport.de/archiv/2005/12/06/schabigster-schauspieler-ever-norbert-meier/\" title=\"allesaussersport &raquo; Sch&auml;bigster Schauspieler e.v.e.r.: Norbert Meier.\">allesaussersport</a>.</p>"},"next":{"fields":{"title":"Chancellor Redirected","date":"07 Dec 2005","slug":"/2005-12-07-chancellor-redirected/"}},"previous":{"fields":{"title":"Puzzled and Bemused","date":"07 Dec 2005","slug":"/2005-12-07-puzzled-and-bemused/"}}},{"node":{"fields":{"title":"Chancellor Redirected","date":"07 Dec 2005","slug":"/2005-12-07-chancellor-redirected/","tags":"Y2005"},"html":"<p>The web server for <a href=\"http://www.bundeskanzler.de/\">http://www.bundeskanzler.de/</a> will helpfully redirect you if you have forgotten that the Chancellor is now female.</p>\n<p><code>moneyball:~ shanson$ GET http://www.bundeskanzler.de/<br />\n&lt;HTML&gt;<br />\n&lt;HEAD&gt;<br />\n&lt;META HTTP-EQUIV=\"Content-Type\" CONTENT=\"text/html; charset=iso-8859-1\"&gt;<br />\n&lt;script LANGUAGE=\"JavaScript\"&gt;<br />\n&lt;!--<br />\ntop.location = \"http://www.bundeskanzlerin.de\";<br />\n//--&gt;<br />\n&lt;/script&gt;<br />\n        &lt;noscript&gt;<br />\n                &lt;META HTTP-EQUIV=\"REFRESH\" CONTENT=\"0;URL=http://www.bundeskanzlerin.de\"&gt;<br />\n        &lt;/noscript&gt;<br />\n&lt;/head&gt;<br />\n&lt;body BGCOLOR=\"White\"&gt;<br />\n&lt;/body&gt;<br />\n&lt;/html&gt;</code></p>"},"next":{"fields":{"title":"The Two Most Powerful Women On The Face Of The Earth","date":"06 Dec 2005","slug":"/2005-12-06-the-two-most-powerful-women-on-the-face-of-the-earth/"}},"previous":{"fields":{"title":"Head Butt","date":"07 Dec 2005","slug":"/2005-12-07-head-butt/"}}}],"pathPrefix":"","first":false,"last":false,"index":259,"pageCount":1101}}

